<template>
  <v-app id="app">
    <v-alert
      :value="alert"
      dense
      dark
      :type="alertType"
      :color="alertColor"
      class="appAlert mt-xs-5 mt-md-1 mr-2"
      transition="scale-transition"
    >
      {{ alertMsg }}
    </v-alert>
    <v-app-bar color="#00B06B" dark app>
      <v-toolbar-title>{{$t('home.title')}}</v-toolbar-title>
      <v-spacer />
      <v-btn
        icon
      >
        <v-icon>mdi-information</v-icon>
      </v-btn>
    </v-app-bar>
    <v-main class="mt-0 pt-0 mapMain">
      <v-container fluid fill-height class="mapContainer">
        <MapPane
          ref="mapPane"
          @shelterInfo="shelterDetail"
          @disasterInfo="disasterDetail"
          @fetchRouteStatus="showRFetchAlt"
          @markerMoved="emitLocatedToUpload($event)"
          @pitchLocStatusForHome="locationAlert($event)"
          @pitchLocStatusForNav="emitLocStatToRoute($event)"
          @emitSortedShelter="emitSheltersToNav($event)"
        />
        <RouteNavigation
          class="position: relative;"
          ref="routeNav"
          @runDrawLine="emitRouteNavToMap($event)"
          @fireGeoLocTrig="triggerMapPaneGeoLoc"
          @getNearest="runFindShelter($event)"
          @emitToMapShowRt="startRout($event)"
          @usrCoordsToMap="emitUsrCrdToMap($event)"
          @clearRoute="runClearRoute()"
          @routePaneOpened="changeMapWidth()"
        />
        <v-row justify="center" class="position: relative;" :style="{'margin-left': `${buttonLeftMargin}px`}">
          <v-btn
            color="#00B06B"
            dark
            x-large
            id="routeBtn"
            class="routeNavBtn"
            @click="onNavBtnClicked()"
          >
            <v-icon
              left
              size="30"
              dark
            >
              {{ mdiRun }}
            </v-icon>
            {{ $t('routeNav.startNav') }}
          </v-btn>
        </v-row>
        <UploadSheet
          ref="uploadSheet"
          @closeProvideInfo="emitProvideInfoEvt"
          @provideInfoStats="handleProvInfoStats($event)"
        />
      </v-container>
    </v-main>

    <v-bottom-sheet
      v-if="mobileOrNo"
      v-model="shelterDetailSheet"
      inset
    >
      <v-sheet
        class="text-center"
        height="200px"
      >
        <v-btn
          class="mt-6"
          text
          color="error"
          @click="sheet = !sheet"
        >
          close
        </v-btn>
        <div class="my-3">
          This is a bottom sheet using the inset prop
        </div>
      </v-sheet>
    </v-bottom-sheet>
  </v-app>
</template>

<script>
import { mdiRun } from '@mdi/js'
// マップコンポーネント読み込み
import MapPane from '@/components/modules/MapPane'
import UploadSheet from '@/components/modules/UploadSheet'
import RouteNavigation from '@/components/modules/RouteNavigation'
import { isMobileOnly } from 'mobile-device-detect'

export default {
  name: 'about',
  components: {
    MapPane,
    UploadSheet,
    RouteNavigation
  },
  data () {
    return {
      mdiRun: mdiRun,
      shelterDetailSheet: false,
      alert: false,
      alertMsg: null,
      alertType: null,
      alertColor: null,
      mobileOrNo: isMobileOnly,
      navPaneBool: false
    }
  },
  computed: {
    buttonLeftMargin: function () {
      return this.shelterDetailSheet ? 256 : 0
    }
  },
  mounted () {
  },
  methods: {
    showAlert (message, type, color, duration) {
      this.alertMsg = message
      this.alertType = type
      this.alertColor = color
      this.alert = true
      window.setTimeout(() => {
        this.alert = false
      }, duration)
    },
    locationAlert (e) {
      switch (true) {
        case /Origin does not/.test(e.mapboxEvent.message):
          this.showAlert('現在地の取得に失敗しました。', 'error', 'red', 5000)
          break
      }
    },
    shelterDetail () {
      this.shelterDetailSheet = true
    },
    disasterDetail () {
      this.shelterDetailSheet = true
    },
    emitRouteNavToMap (e) {
      this.$refs.mapPane.handleEvFromNav(e)
    },
    showRFetchAlt (e) {
      if (e.status === 200) {
        this.showAlert('ルートの取得に成功しました。', 'success', 'green', 5000)
      } else {
        this.showAlert('ルートの取得に失敗しました。', 'error', 'red', 5000)
      }
    },
    emitLocatedToUpload (e) {
      this.$refs.uploadSheet.handleMarkerClick(e)
    },
    emitProvideInfoEvt () {
      this.$refs.mapPane.handleProvInfoMode()
    },
    handleProvInfoStats (e) {
      switch (e) {
        case 'uploadCompleted':
          this.showAlert('アップロードが完了しました', 'success', 'green', 4000)
          break
        case 'uploadFailed':
          this.showAlert('アップロードに失敗しました', 'error', 'red', 4000)
          break
      }
    },
    triggerMapPaneGeoLoc () {
      this.$refs.mapPane.triggerGeolocate()
    },
    runFindShelter (e) {
      this.$refs.mapPane.findNearShelter(e)
    },
    emitSheltersToNav (e) {
      this.$refs.routeNav.sheltersDataFromMap(e)
    },
    startRout (e) {
      this.$refs.mapPane.drawLine(e)
    },
    emitUsrCrdToMap (e) {
      this.$refs.mapPane.getUserCoordsByNav(e)
    },
    runClearRoute () {
      this.$refs.mapPane.clearRouteLine()
    },
    changeMapWidth () {
      this.shelterDetailSheet = !this.shelterDetailSheet
      this.$refs.mapPane.changeMapWidth()
    },
    onNavBtnClicked () {
      this.$refs.routeNav.onBtnsClicked()
    }
  }
}
</script>

<style scoped>
  .mapMain {
    z-index: 0;
    overflow: hidden;
  }
  .mapContainer {
    height: calc(100% - 56px);
    margin-top: 56px;
    padding: 0;
    overflow: hidden;
    position: relative;
  }
  .appAlert {
    z-index: 1;
    position: absolute;
    top: calc(56px + 2vh);
    right: 0px;
  }
  .geoLocBtn {
    right: 25px;
    bottom: 110px;
  }
  .app::-webkit-scrollbar {
    display: none;
  }
  .routeNavBtn {
    z-index: 800;
    position: absolute;
    bottom: 40px;
  }
</style>



