<template>
  <div v-if="mobileOrNo">
    <v-bottom-sheet
      hide-overlay
      persistent
      no-click-animation
      v-model="showUpldSheet"
    >
      <v-card
        height="50vh"
        tile
      >
        <v-subheader>災害情報の提供</v-subheader>

        <v-divider />
        <div class="py-2">
          {{ `${addressStr ? `選択された場所: ${addressStr} 付近`: '選択された場所: ?'}` }}
        </div>
        <v-divider />

        <v-container fluid>
          <v-row justify="center">
            <v-col
              cols="10"
              sm="6"
              md="4"
            >
              <template v-if="!previewImageData">
                <v-file-input
                  class="py-0"
                  v-model="inputRawFiles"
                  label="現場の写真(任意)"
                  placeholder="画像を選択"
                  @change="previewImage"
                />
              </template>
              <template v-else>
                <div style="position:relative">
                  <v-img
                    width="90px"
                    max-height="80"
                    :src="previewImageData"
                  >
                  </v-img>
                  <v-btn
                    icon
                    @click.prevent="inputRawFiles = null; previewImageData = null"
                    class="deleteImg"
                  >
                    <v-icon>mdi-close</v-icon>
                  </v-btn>
                </div>
              </template>
            </v-col>
            <v-col
              cols="12"
              sm="12"
              md="12"
            >
              <v-divider />
            </v-col>

            <v-col
              cols="12"
              sm="6"
              md="4"
            >
              <v-textarea
                v-model="description"
                outlined
                name="input-7-4"
                label="どのような災害が起きていますか？"
                auto-grow
                required
                :rules="formRules"
                class="mt-2"
                height="5vh"
              ></v-textarea>
            </v-col>
            <div class="mb-1">
              <v-btn
                text
                @click.prevent="onCloseBtnClicked"
              >
                キャンセル
              </v-btn>
              <v-btn
                color="primary"
                :disabled="!(description)"
                @click="uploadInfo"
              >
                確定
              </v-btn>
            </div>
          </v-row>
        </v-container>
      </v-card>
    </v-bottom-sheet>
  </div>
  <div v-else-if="showUpldSheet && !mobileOrNo">
    <v-card
        height="calc(100vh - 60px)"
        class="mx-auto; mt-1"
        width="256px"
        light
    >
        <v-subheader class="pr-0">災害情報の提供</v-subheader>

        <v-navigation-drawer
          light
          permanent
          v-model="showUpldSheet"
          style="max-height: calc(100% - 56px)"
        >

        <v-divider />
        <div class="py-2">
          {{ `${addressStr ? `選択された場所:`: ''}` }}
        <v-divider></v-divider>
        <div class="py-1">
          {{ `${addressStr ? `${addressStr} 付近`: '?'}` }}
        </div>
        </div>
        <v-divider />

        <v-container fluid>
          <v-row justify="center">
              <template v-if="!previewImageData">
                <v-file-input
                  class="py-0"
                  v-model="inputRawFiles"
                  label="現場の写真(任意)"
                  placeholder="画像を選択"
                  @change="previewImage"
                />
              </template>
              <template v-else>
                <div style="position:relative">
                  <v-img
                    width="90px"
                    max-height="80"
                    :src="previewImageData"
                  >
                  </v-img>
                  <v-btn
                    icon
                    @click.prevent="inputRawFiles = null; previewImageData = null"
                    class="deleteImg"
                  >
                    <v-icon>mdi-close</v-icon>
                  </v-btn>
                </div>
              </template>

            <!-- <v-col
              cols="12"
              sm="6"
              md="4"
            > -->
            <!-- </v-col> -->
          </v-row>
              <v-divider />
              <v-subheader>どのような災害が起きていますか？</v-subheader>
              <v-textarea
                v-model="description"
                outlined
                name="input-7-4"
                label="どのような災害が起きていますか？"
                auto-grow
                required
                :rules="formRules"
                class="mt-0"
                height="5vh"
              ></v-textarea>
        </v-container>
        <div class="position: relative">
          <v-btn
            text
            @click.prevent="onCloseBtnClicked"
            absolute bottom left
          >
            キャンセル
          </v-btn>
          <v-btn
            color="primary"
            absolute bottom right
            :disabled="!(description)"
            @click="uploadInfo"
          >
            確定
          </v-btn>
        </div>
        </v-navigation-drawer>
      </v-card>
  </div>
</template>

<script>
import { isMobileOnly } from 'mobile-device-detect'
import i18next from 'i18next'
import axios from 'axios'
import firebase from 'firebase'
export default {
  name: 'provideInfo',
  data () {
    return {
      mobileOrNo: isMobileOnly,
      showUpldSheet: false,
      disasterCoords: null,
      previewImageData: null,
      inputRawFiles: null,
      formRules: [
        v => !!v || '入力必須です'
      ],
      addressStr: null,
      description: null,
      cloudinaryImgUrl: null,
      imgSecUrl: null,
      imgUrl: null,
      submitTime: null
    }
  },
  computed: {
    clUrl: function () {
      return `https://api.cloudinary.com/v1_1/${process.env.VUE_APP_CLOUDINARY_CLOUD_NAME}/image/upload`
    }
  },
  methods: {
    async handleMarkerClick (e) {
      this.showUpldSheet = true
      this.disasterCoords = e
      await this.getAddressStr([e[1], e[0]])
    },
    onCloseBtnClicked () {
      this.showUpldSheet = false
      this.$emit(('closeProvideInfo'))
    },
    async getAddressStr (coords) {
      const userLang = i18next.language
      await axios.get(`/v1/geo_search/${userLang}/${coords}`)
        .then(response => {
          if (response.data.features[0]) {
            var resAddrStr = response.data.features[0][`place_name_${userLang}`]
            switch (userLang) {
              case 'ja':
                var splitedAddrStr = resAddrStr.split(', ')
                if (splitedAddrStr[0] === '日本') { // まれに、住所が英語フォーマットになっていることがあるので置換する
                  this.addressStr = splitedAddrStr.filter(a => {
                    return a !== '日本'
                  })[0]
                } else {
                  this.addressStr = splitedAddrStr.filter(a => {
                    return a !== '日本'
                  }).reverse().join('')
                }
                break
              case 'en':
                this.addressStr = resAddrStr
                break
            }
          } else {
            this.addressStr = null
          }
        })
    },
    previewImage () {
      this.previewImageData = URL.createObjectURL(this.inputRawFiles)
    },
    async uploadInfo () {
      this.onCloseBtnClicked()
      const { point } = require('@turf/helpers')
      const usrDescription = this.description
      var disasterPoint = point(this.disasterCoords)
      if (this.inputRawFiles) {
        const disasterPic = this.inputRawFiles
        const formData = new FormData()
        formData.append('file', disasterPic)
        formData.append('upload_preset', process.env.VUE_APP_CLOUDINARY_UPLOAD_PRESET)
        formData.append('api_key', process.env.VUE_APP_CLOUNDINRY_API_kEY)
        await axios.post(this.clUrl, formData, {
          headers: { 'X-Requested-With': 'XMLHttpRequest' }
        })
          .then(response => {
            this.imgSecUrl = response.data.secure_url
            this.imgUrl = response.data.url
            this.submitTime = response.data.created_at
            this.$emit('provideInfoStats', 'imgUploadSucceeded')
          })
          .catch(error => {
            this.$emit('provideInfoStats', error)
          })
      }
      disasterPoint.properties = {
        'submit_time': this.submitTime,
        'img_secure_url': this.imgSecUrl,
        'img_url': this.imgUrl,
        'user_description': usrDescription
      }
      await firebase.database().ref('features/').push(disasterPoint).then(() => {
        this.$emit('provideInfoStats', 'uploadCompleted')
        this.inputRawFiles = null
        this.imageData = null
      }).catch(() => {
        this.$emit('provideInfoStats', 'uploadFailed')
      })
    }
  },
  mounted () {
  }
}
</script>

<style scoped>
  .closeBtn {
    top: 5px;
    right: 5px;
    position: absolute;
  }
  .deleteImg {
    top: 1px;
    left: 95px;
    position: absolute;
  }
  .selectedText {
    position: absolute;
    left: 2px;
    margin-bottom: 10px;
  }
  .doneBtn {
    position: fixed;
    bottom: 15px;
    left: 50vw;
  }
</style>



