<template>
  <div id="map-wrap"
    :style="{'height': `calc(100% - ${uploadMapHeight}vh)`, 'width': `calc(100% - ${uploadMapWidth}px)`, 'margin-left':`${uploadMapWidth}px`}"
  >
    <MglMap
      ref="map"
      container="map-wrap"
      :center.sync="mapOptions.center"
      :bearing="mapOptions.bearing"
      :zoom="mapOptions.zoom"
      :accessToken="accessToken"
      :mapStyle="mapStyle"
      class="mapPane"
      @load="onMapLoaded"
      @click="onMapClicked"
    >
      <MglNavigationControl
        position="top-right"
        :showZoom="false"
      />
      <MglGeolocateControl
        ref="geoLoc"
        :trackUserLocation="true"
        :positionOptions="positionOptions"
        @geolocate="fetchLocationStatus"
        @error="fetchLocationStatus"
      />

      <!-- 情報提供用マーカー -->
      <MglMarker
        v-if="showMarker"
        ref="marker"
        :draggable="true"
        anchor="bottom"
        :coordinates="infoMarkerCoords"
        @dragend="onMarkerDragEnd"
      >
      </MglMarker>

      <!-- ユーザーが選択した避難所表示用マーカー -->
      <MglMarker
        v-if="showRouteLine"
        anchor="bottom"
        color="red"
        :coordinates="selectedShelterMarker['coords']"
      >
        <MglPopup
          :coordinates="selectedShelterMarker['coords']"
          :offset="[0, -42]"
          :closeButton="false"
          :showed="true"
          :onlyText="true"
        >
         <v-card><div>{{selectedShelterMarker['popup']}}</div></v-card>
        </MglPopup>
      </MglMarker>

      <!-- ルート表示レイヤー -->
      <MglGeojsonLayer
        v-if="showRouteLine"
        ref="routeGJsonLayer"
        before="shelters"
        sourceId="routeLine"
        :source.sync="routeGeojson.source"
        layerId="routeLine"
        :layer="routeGeojson.layer"
        :clearSource="false"
        :replace="true"
      >
      </MglGeojsonLayer>

      <!-- 避難所レイヤー -->
      <MglGeojsonLayer
        ref="sheltersLayer"
        sourceId="shelters"
        :source.sync="sheltersGeojson.source"
        layerId="shelters"
        :layer="sheltersGeojson.layer"
        @click="shelterMrkClicked"
      >
      </MglGeojsonLayer>

      <!-- 災害発生レイヤー -->
      <MglGeojsonLayer
        v-if="showDisasterPt"
        ref="disasterPoints"
        sourceId="disasterPoints"
        :source.sync="disasterPtGeoJson.source"
        layerId="disasterPoints"
        :layer.sync="disasterPtGeoJson.layer"
        :replace="true"
        @click="disasterPtClicked"
      >
      </MglGeojsonLayer>
    </MglMap>
  </div>
</template>

<script>
import firebase from 'firebase'
import Mapbox from 'mapbox-gl'
import { mdiFlagCheckered } from '@mdi/js'
import {
  MglMap,
  MglNavigationControl,
  MglGeolocateControl,
  MglGeojsonLayer,
  MglMarker,
  MglPopup
} from 'vue-mapbox'
import 'mapbox-gl/dist/mapbox-gl.css'
import renkeiShelterGeojson from '@/assets/shelter_geojson/renkei_shisetsu_hinan.geojson'
import toyamaShelter from '@/assets/shelter_geojson/toyama_prefecture/hinanjo20181201.geojson'
import sapporoNishi from '@/assets/shelter_geojson/sapporo/hinan_nisi_sitei_hinan.geojson'
import axios from 'axios'
import { isMobileOnly } from 'mobile-device-detect'

export default {
  name: 'MapPane',
  components: {
    MglMap,
    MglNavigationControl,
    MglGeolocateControl,
    MglGeojsonLayer,
    MglMarker,
    MglPopup
  },
  data () {
    return {
      mdiFlagCheckered: mdiFlagCheckered,
      accessToken: 'pk.eyJ1IjoidWh1Y3JlYW0iLCJhIjoiY2tqb2JjaGNnMTI2YTJ5bjBkeDNpd25oeSJ9.mwWqrpHBw4gbLEdlVOeJ9g',
      mapStyle: 'mapbox://styles/uhucream/ckjttyx3t0ojq19o1f9zh7j1v',
      mapOptions: {
        center: [139.7009177, 35.6580971],
        bearing: 0,
        pitch: 0,
        zoom: 10
      },
      mobileOrNo: isMobileOnly,
      userLocation: [],
      disasterCoords: [],
      disPointsInsideRoute: [],
      selectedShelterMarker: {
        'popup': null,
        'coords': null
      },
      showRouteLine: false,
      routePane: false,
      showMarker: false,
      tempArray: [],
      showDisasterPt: false,
      infoMarkerCoords: [139.7009177, 35.6580971],
      positionOptions: {
        enableHighAccuracy: true
      },
      sortedSheltersData: undefined,
      sheltersGeojson: {
        source: {
          type: 'geojson',
          data: null
        },
        layer: {
          id: 'shelters',
          type: 'symbol',
          'minzoom': 10,
          'layout': {
            'visibility': 'visible',
            'icon-image': 'custom-marker',
            'icon-size': ['interpolate', ['linear'], ['zoom'], 10, 1, 15, 1],
            'icon-anchor': 'bottom',
            'text-field': [
              'format',
              ['get', 'name'],
              {'font-scale': 0.8}
            ],
            'text-font': [
              'DIN Offc Pro Italic',
              'Arial Unicode MS Regular'
            ],
            'text-anchor': 'top'
          }
        }
      },
      routeGeojson: {
        source: {
          type: 'geojson',
          data: null
        },
        layer: {
          id: 'routeLine',
          source: 'routeLine',
          type: 'line',
          'layout': {
            'line-cap': 'round',
            'line-join': 'round'
          },
          'paint': {
            'line-color': '#5B95F7',
            'line-width': 5
          }
        }
      },
      routeLineBackup: {
        source: {
          type: 'geojson',
          data: null
        },
        layer: {
          id: 'routeLine',
          source: 'routeLine',
          type: 'line',
          'layout': {
            'line-cap': 'round',
            'line-join': 'round'
          },
          'paint': {
            'line-color': '#5B95F7',
            'line-width': 5
          }
        }
      },
      disasterPtGeoJson: {
        source: {
          type: 'geojson',
          data: []
        },
        layer: {
          id: 'disasterPoints',
          source: 'disasterPoints',
          type: 'circle',
          'paint': {
            'circle-radius': 10,
            'circle-color': '#223b53'
          }
        }
      }
    }
  },
  computed: {
    uploadMapHeight: function () {
      if (this.mobileOrNo) {
        return this.showMarker ? 50 : 0
      }
    },
    uploadMapWidth: function () {
      if (!this.mobileOrNo) {
        return this.showMarker || this.routePane ? 256 : 0
      }
    }
  },
  watch: {
    tempArray: function () {
      const featureCollection = require('turf-featurecollection')
      this.$set(this.disasterPtGeoJson.source, 'data', featureCollection(this.tempArray))
    },
    userLocation: function () {
      axios.get(`/v1/geo_search/en/${this.userLocation[1]},${this.userLocation[0]}`)
        .then(response => {
          Object.keys(response.data.features[0].context).forEach(val => {
            switch (response.data.features[0].context[val].text) {
              case 'Sapporo':
                this.$set(this.sheltersGeojson.source, 'data', sapporoNishi)
                this.idAssignFunc()
                break
              case 'Kanazawa':
                this.$set(this.sheltersGeojson.source, 'data', renkeiShelterGeojson.features)
                this.idAssignFunc()
                break
              case 'Toyama':
                this.$set(this.sheltersGeojson.source, 'data', toyamaShelter.features)
                this.idAssignFunc()
                break
            }
          })
        })
      if (this.showRouteLine) { // ユーザーの動きに合わせて線を伸ばしたり縮める
        const turf = require('@turf/turf')
        var destination = turf.point(this.routeGeojson.source.data.metadata.query.coordinates[1])
        console.log(this.routeGeojson, this.routeLineBackup)

        this.$set(this.routeGeojson.source.data.features[0], 'geometry', turf.lineSlice(destination, turf.point(this.userLocation), this.routeLineBackup.source.data.features[0]).geometry)
      }
    }
  },
  methods: {
    onMapLoaded (event) {
      const map = event.map
      map.loadImage(
        'https://docs.mapbox.com/mapbox-gl-js/assets/custom_marker.png',
        function (error, image) {
          if (error) throw error
          map.addImage('custom-marker', image)
        }
      )
    },
    fetchAltUserLocation () {
      axios.get('/v1/ip_search')
        .then(response => {
          this.$set(this.userLocation, 0, response.data.coordinates.lon)
          this.$set(this.userLocation, 1, response.data.coordinates.lat)
          this.$set(this.mapOptions, 'center', [this.userLocation[0], this.userLocation[1]])
          this.$refs.map.actions.panTo({center: this.mapOptions.center})
        })
        .catch(error => {
          this.$emit('locationErr', error)
        })
    },
    arrayAssignFunc: function (data) {
      var pushArray = []
      var rootList = data.val()
      var keys = Object.keys(rootList)
      for (var i = 0; i < keys.length; i++) {
        var k = keys[i]
        pushArray.push(rootList[k])
      }
      this.tempArray = pushArray
      if (this.disasterPtGeoJson.source.data) {
        this.showDisasterPt = true
      }
    },
    idAssignFunc () {
      var count = 0
      this.sheltersGeojson.source.data.features.forEach(shelter => {
        Object.defineProperty(shelter, 'id', {
          value: count,
          writable: true,
          enumeratable: true,
          configurable: true
        })
        count++
      })
    },
    onMapClicked (e) {
      const coordinates = e.mapboxEvent.lngLat
      this.infoMarkerCoords = [coordinates.lng, coordinates.lat]
      this.showMarker = true
      e.component.actions.panTo(this.infoMarkerCoords, { 'offset': [this.mobileOrNo ? 0 : -256, this.mobileOrNo ? -200 : 0] })
      this.$emit('markerMoved', this.infoMarkerCoords)
    },
    onMarkerDragEnd (e) {
      const coordinates = e.mapboxEvent.target._lngLat
      this.infoMarkerCoords = [coordinates.lng, coordinates.lat]
      this.$refs.map.actions.panTo(this.infoMarkerCoords, { 'offset': [this.mobileOrNo ? 0 : -256, this.mobileOrNo ? -200 : 0] })
      this.$emit('markerMoved', this.infoMarkerCoords)
    },
    handleProvInfoMode () {
      this.$refs.marker.remove()
      this.showMarker = false
    },
    fetchLocationStatus (e) {
      if (e.mapboxEvent.coords) {
        this.userLocation = [e.mapboxEvent.coords.longitude, e.mapboxEvent.coords.latitude]
      }
      this.$emit('pitchLocStatusForHome', e)
    },
    shelterMrkClicked (e) {
      this.$emit('shelterInfo', e.mapboxEvent.features)
    },
    disasterPtClicked (e) {
      this.$emit('disasterInfo', e.mapboxEvent.features)
    },
    triggerGeolocate () {
      this.$refs.geoLoc.trigger()
    },
    findNearShelter () {
      const { point } = require('@turf/helpers')
      const distance = require('@turf/distance').default
      var options = { units: 'kilometers' }
      var sheltersData = this.sheltersGeojson.source.data
      sheltersData.features.forEach(shelter => {
        Object.defineProperty(shelter, 'distance', {
          value: distance(point([this.userLocation[0], this.userLocation[1]]), shelter.geometry, options),
          writable: true,
          enumeratable: true,
          configurable: true
        })
      })
      sheltersData.features.sort(function (a, b) {
        if (a.distance > b.distance) {
          return 1
        }
        if (a.distance < b.distance) {
          return -1
        }
        return 0
      })
      this.sortedSheltersData = sheltersData
      this.$emit('emitSortedShelter', this.sortedSheltersData)
    },
    async getRouteGeojson (arg) {
      await axios.post('/v1/route', {...arg})
        .then(response => {
          const responseData = response.data
          this.$set(this.routeGeojson.source, 'data', responseData)
          this.$set(this.routeLineBackup.source, 'data', responseData)
          this.$emit('fetchRouteStatus', response)
        })
        .catch(error => {
          this.$emit('fetchRouteStatus', error)
        })
    },
    easeInOutCirc (x) { // https://easings.net より
      if (x < 0.5) {
        return (1 - Math.sqrt(1 - Math.pow(2 * x, 2))) / 2
      } else {
        return (Math.sqrt(1 - Math.pow(-2 * x + 2, 2)) + 1) / 2
      }
    },
    findDisPointsOnRoute (point) {
      this.disPointsInsideRoute = []
      var disPtData = this.disasterPtGeoJson.source.data
      const turf = require('@turf/turf')
      var distanceOptions = { units: 'kilometers' }
      var circleCenter = [point[0], point[1]]
      const radius = turf.distance(turf.point([this.userLocation[0], this.userLocation[1]]), turf.point(circleCenter), distanceOptions)
      var circleOptions = {steps: 60, units: 'kilometers', properties: {}}
      var routeCircle = turf.circle(circleCenter, radius, circleOptions)
      Object.keys(disPtData.features).forEach(index => {
        if (turf.booleanPointInPolygon(disPtData.features[index].geometry, routeCircle)) {
          this.disPointsInsideRoute.push([disPtData.features[index].geometry.coordinates[1], disPtData.features[index].geometry.coordinates[0]])
        }
      })
    },
    async drawLine (shelter) {
      this.showRouteLine = false
      var shelterCoords = shelter.geometry.coordinates
      this.findDisPointsOnRoute(shelterCoords)
      this.$set(this.selectedShelterMarker, 'coords', shelterCoords)
      this.$set(this.selectedShelterMarker, 'popup', shelter.properties.name)
      if (this.disPointsInsideRoute) {
        await this.getRouteGeojson({'coordinates': [[this.userLocation[0], this.userLocation[1]], [shelterCoords[0], shelterCoords[1]]], 'avoided_points': this.disPointsInsideRoute})
      } else {
        await this.getRouteGeojson({'coordinates': [[this.userLocation[0], this.userLocation[1]], [shelterCoords[0], shelterCoords[1]]]})
      }
      if (this.routeGeojson) {
        this.$set(this.sheltersGeojson.layer['layout'], 'visibility', 'none')
        var coordinates = this.routeGeojson.source.data.features[0].geometry.coordinates
        this.$set(this.routeGeojson.source.data.features[0].geometry, 'coordinates', [coordinates[0]])

        this.showRouteLine = true
        const routeBbox = this.routeGeojson.source.data.bbox
        this.$refs.map.actions.fitBounds([[routeBbox[0], routeBbox[1]], [routeBbox[2], routeBbox[3]]], {
          'padding': 100
        })

        // ルートの線のアニメーション
        var i = 0
        var timer = window.setInterval(() => {
          if (i < coordinates.length) {
            this.$set(this.routeGeojson.source.data.features[0].geometry.coordinates, i, coordinates[i])
            i++
          } else {
            window.clearInterval(timer)
          }
        }, this.easeInOutCirc(10 + i))
        this.$emit('routeDistance', this.routeGeojson.source.data.features[0].properties.summary.distance)
      }
    },
    clearRouteLine () {
      this.showRouteLine = false
      this.$set(this.sheltersGeojson.layer['layout'], 'visibility', 'visible')
      if (this.routeGeojson.source.data !== null) {
        this.$refs.routeGJsonLayer.remove()
        this.routeGeojson.source.data.features.splice(0, this.routeGeojson.source.data.length)
        this.selectedShelter.source.data.features.splice(0, this.routeGeojson.source.data.length)
        this.routeGeojson.source.data = this.selectedShelter.source.data = null
      }
      if (this.sortedSheltersData !== null) {
        this.sortedSheltersData = null
      }
    },
    getUserCoordsByNav (e) {
      this.userLocation = [e.lng, e.lat]
    },
    changeMapWidth () {
      this.routePane = !this.routePane
    },
    collectAllDisPt () {
    }
  },
  created () {
    this.mapbox = Mapbox
    this.map = null
    firebase.database().ref('features/').on('value', (snapshot) => this.arrayAssignFunc(snapshot))
  },
  mounted () {
    this.fetchAltUserLocation()
  }
}
</script>

<style>
.mapboxgl-canvas {
  outline: transparent;
  left: 0;
}
#map-wrap {
  position: absolute;
  height: 100%;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
}

#map-wrap .mgl-map-wrapper {
  position: absolute;
  height: 100%;
  top: 0;
  left: 0;
  width: 100%;
}

#map-wrap .mgl-map-wrapper .mapboxgl-map {
  height: 100%;
  left: 0;
  overflow: visible;
  position: absolute;
  top: 0;
  width: 100%;
}
#mapboxgl-ctrl-geolocate {
  display: none;
}
</style>



