<template>
  <div>
    <!-- モバイルレイアウト -->
    <template v-if="mobileOrNo">
      <v-bottom-sheet
      no-click-animation
      v-model="sheltersCard"
      >
        <v-card
          :height="bottomCardHeight"
          class="mx-auto"
        >
          <v-subheader>
            最寄りの避難所
            <v-spacer />
            <!-- <v-row align-content="center"> -->
              <v-btn
                text
                color="primary"
                class="pa-0 ma-0"
                :disabled="progressLinear"
                @click="clearAllInfo()"
              >
                キャンセル
              </v-btn>
            <!-- </v-row> -->
          </v-subheader>

          <v-divider />
          <template v-if="progressLinear == true">
            <v-row
              justify="center"
              class="pt-4"
            >
              <v-col cols="6">
                <v-progress-linear
                  color="blue accent-4"
                  indeterminate
                  rounded
                  height="5"
                ></v-progress-linear>
              </v-col>
              <v-col
                class="subtitle-1 text-center"
                cols="12"
              >
                最寄りの避難所を探しています
              </v-col>
            </v-row>
          </template>
          <template v-else>
            <v-list
              two-line
              class="overflow-y-auto"
              style="max-height: calc(50vh - 48px)"
            >
              <v-list-item-group
                color="primary"
                active-class="blue--text"
              >
                <template v-for="(feature, index) in sortedShelters">
                  <v-list-item
                    @click.stop="showRouteLine(index)"
                    :key="index"
                  >
                    <v-list-item-content
                      class="text-left"
                    >
                      <v-list-item-title v-text="feature.properties.name"></v-list-item-title>

                      <v-list-item-subtitle
                        class="text--primary"
                        v-text="feature.properties['電話番号']"
                      ></v-list-item-subtitle>

                      <v-list-item-subtitle v-text="feature.properties['ジャンル1']"></v-list-item-subtitle>
                    </v-list-item-content>

                    <v-list-item-action>
                      <v-list-item-action-text v-text="distanceStr(feature.distance)"></v-list-item-action-text>
                      <v-btn
                        icon
                        @click.stop="showShelterInfo(feature.properties)"
                      >
                        <v-icon>mdi-information</v-icon>
                      </v-btn>
                    </v-list-item-action>
                  </v-list-item>

                  <v-divider
                    v-if="index < sortedShelters.length - 1"
                    :key="index"
                  ></v-divider>
                </template>
              </v-list-item-group>
            </v-list>
          </template>
        </v-card>
      </v-bottom-sheet>
    </template>

    <!-- PC/タブレットレイアウト -->
    <template v-else-if="sheltersCard && mobileOrNo === false">
      <v-card
        height="calc(100vh - 60px)"
        class="mx-auto; mt-1"
        width="256px"
        light
      >
        <v-subheader class="pr-0">最寄りの避難所</v-subheader>
        <v-navigation-drawer
          light
          permanent
          v-model="sheltersCard"
          style="max-height: calc(100% - 56px)"
        >

          <template v-if="progressLinear == true">
            <v-container fill-height>
              <v-row
                justify="center"
                align-content="center"
              >
                <v-col cols="6">
                  <v-progress-circular
                    :size="60"
                    color="blue accent-4"
                    indeterminate
                  ></v-progress-circular>
                </v-col>
                <v-col
                  class="subtitle-1 text-center"
                  cols="12"
                >
                  最寄りの避難所を探しています
                </v-col>
              </v-row>
            </v-container>
          </template>
          <template v-else>
            <v-list
              two-line
              class="overflow-y-auto; py-0"
            >
              <v-list-item-group
                color="primary"
                active-class="blue--text"
              >
                <template v-for="(feature, index) in sortedShelters">
                  <v-list-item
                    @click.stop="showRouteLine(index)"
                    :key="index"
                  >
                    <v-list-item-content
                      class="text-left"
                    >
                      <v-list-item-title v-text="feature.properties.name"></v-list-item-title>

                      <v-list-item-subtitle
                        class="text--primary"
                        v-text="feature.properties['電話番号']"
                      ></v-list-item-subtitle>

                      <v-list-item-subtitle v-text="feature.properties['ジャンル1']"></v-list-item-subtitle>
                    </v-list-item-content>

                    <v-list-item-action>
                      <v-list-item-action-text v-text="distanceStr(feature.distance)"></v-list-item-action-text>
                      <v-btn
                        icon
                        @click.stop="showShelterInfo(feature.properties)"
                      >
                        <v-icon>mdi-information</v-icon>
                      </v-btn>
                    </v-list-item-action>
                  </v-list-item>

                  <v-divider
                    v-if="index < sortedShelters.length - 1"
                    :key="index"
                  ></v-divider>
                </template>
              </v-list-item-group>
            </v-list>
          </template>

          <template v-if="progressLinear == false" v-slot:append>
            <v-divider class="pt-0 mt-0" />
            <div class="pa-2">
              <v-btn
                text
                @click="clearAllInfo()"
              >キャンセル</v-btn>
            </div>
          </template>
        </v-navigation-drawer>
      </v-card>
    </template>

    <!-- 位置情報許可を促すモーダル -->
    <v-dialog
      v-model="geoLocModal"
      max-width="300"
    >
      <v-card>
        <v-card-title class="headline">
          位置情報サービスを許可しますか？
        </v-card-title>
        <v-card-text>
          近くの避難所を表示するため、位置情報サービスを許可してください。
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            color="primary"
            text
            @click="okBtn()"
          >
            OK
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { isMobileOnly } from 'mobile-device-detect'
export default {
  data () {
    return {
      mobileOrNo: isMobileOnly,
      moveBtnOrNo: false,
      sheltersCard: false,
      geoLocModal: false,
      coordinates: null,
      progressLinear: true,
      rawSortedData: null,
      sortedShelters: [],
      isOkBtnClicked: false,
      bottomCardHeight: '20vh'
    }
  },
  methods: {
    okBtn () {
      this.geoLocModal = false
      this.isOkBtnClicked = true
      this.onBtnsClicked()
    },
    onBtnsClicked (e) { // 避難所に向かう ボタンを押されたとき
      if (!this.isOkBtnClicked && this.rawSortedData === null) { // ページ読み込み後okボタンが押されてない場合
        this.geoLocModal = true
      } else {
        if (this.mobileOrNo === false) {
          this.$emit('routePaneOpened')
        }
        this.sheltersCard = true
        this.$emit('fireGeoLocTrig')
        this.$getLocation({
          enableHighAccuracy: true
        })
          .then(coords => {
            this.$emit('usrCoordsToMap', coords)
            this.getNearShelters()
          })
          .catch(err => {
            console.error(err)
            this.sheltersCard = false
          })
      }
    },
    sheltersDataFromMap (e) {
      this.rawSortedData = e
    },
    getNearShelters () {
      this.$emit('getNearest', this.coordinates)
      if (this.rawSortedData) {
        Object.keys(this.rawSortedData.features).forEach(index => {
          this.$set(this.sortedShelters, index, this.rawSortedData.features[index])
        })
        window.setTimeout(() => {
          this.progressLinear = false
          this.bottomCardHeight = '50vh'
        }, 900)
      }
    },
    showRouteLine (i) {
      this.sheltersCard = false
      if (this.mobileOrNo === false) {
        this.$emit('routePaneOpened')
      }
      this.$emit('emitToMapShowRt', this.sortedShelters[i])
    },
    showShelterInfo (i) {
    },
    distanceStr (dist) {
      if (parseInt(dist) === 0) {
        return `${parseInt(dist * 1000)} m`
      } else {
        return `${Math.floor(dist * 10) / 10} km`
      }
    },
    clearAllInfo () {
      this.sheltersCard = false
      if (this.mobileOrNo === false) {
        this.$emit('routePaneOpened')
      }
      this.$emit('clearRoute')
      Object.assign(this.$data, this.$options.data())
      this.isOkBtnClicked = true
      this.mobileOrNo = isMobileOnly
    }
  }
}

</script>



